csui.define('csui/utils/node.links/impl/core.node.links',[
  'csui/utils/url'
], function (Url) {
  'use strict';

  return [
    {
      equals: {
        type: 140
      },
      getUrl: function (node) {
        return node.get('url');
      }
    },
    {
      equals: {
        type: 258
      },
      getUrl: function (node) {
        return Url.combine('search/query_id/', node.get('id'));
      }
    },
    {
      getUrl: function (node) {
        return Url.combine('nodes/', node.get('id'));
      }
    }
  ];
});

